#containerid {
    width: 400px;
    border-style: solid;;
    min-height: 210px;
    border-radius: 5px;
    border-color: rgb(255, 255, 255);
    margin: 0;
    border-width: 0.5px;
    cursor: pointer;
    background: rgb(239,239,242);
background: linear-gradient(90deg, rgba(239,239,242,1) 0%, rgba(238,238,241,1) 25%, rgba(255,255,255,1) 50%, rgba(238,238,241,1) 76%, rgba(239,239,242,1) 100%);

}

#containerid:hover{
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

