.photo {
    height: 5rem;
    margin: 8% auto;
}

.title {
    font-size: 1.2rem;
    text-decoration: underline;
    font-weight: 400;
    margin: 3% 0 0 0;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    width: 210px;
}

.createdAt, .noOfLikes, .category, .name, .price, .location, .subject {
    margin:0;
    width: 100%;
    white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.flexbutton{
    display: flex;
    justify-content: center;
    margin: 0 auto;
}